@font-face {
  font-family: "Roboto Condensed";
  font-weight: normal;
  font-style: bold;
  font-display: swap;
  src: url("../fonts/robotoCondensed-Bold.woff2") format("woff2"), url("../fonts/robotoCondensed-Bold.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/robotoCondensed-Regular.woff2") format("woff2"), url("../fonts/robotoCondensed-Regular.woff") format("woff"); }

@font-face {
  font-family: "Bebas Neue";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/bebasNeue-Regular.woff2") format("woff2"), url("../fonts/bebasNeue-Regular.woff") format("woff"); }

html {
  height: 100%; }

body {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  min-width: 320px;
  height: 100%;
  margin: 0 auto;
  padding: 0;
  font-size: 16px;
  line-height: 22px;
  font-family: "Roboto Condensed", "bebasNeue", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  background-color: #ffffff; }

.container {
  width: 1170px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }
